.background-dashed {
  background-image: linear-gradient(
    -45deg, 
    rgba(255, 255, 255, .2) 25%,
    transparent 25%,
    transparent 50%,
    rgba(255, 255, 255, .2) 50%,
    rgba(255, 255, 255, .2) 75%,
    transparent 75%, 
    transparent
  );
  &.animated-dashed {
    animation: move-bg 1.5s linear infinite;
  }
}